.topMenu{
    width:100%;
    height:40px;
    background:#1D7986;
}
.topMenu--area{
    max-width:1020px;
    height:100%;
    margin:0 auto;

    display:flex;
    justify-content: space-between;
    align-items: center;
}
.topMenu--info{
    display:flex;
    opacity:0.7;
}
.topMenu--email, 
.topMenu--whatsapp{
    display:flex;
    align-items: center;
}
.topMenu--email > span, 
.topMenu--whatsapp > span{
    color:white;
    font-size:0.95rem;
    font-weight:300;
    margin-left:5px;
}
.topMenu--email > svg, 
.topMenu--whatsapp > svg{
    color:white;
    width:20px;
    height:20px;
}
.topMenu--email{
    margin-right:20px;
}
.topMenu--whatsapp > span{
    font-size:0.9rem;
}
.topMenu--contact{
    color:white;
    font-size:0.95rem;
    font-weight:300;
}
.topMenu--contact:hover{
    opacity:0.7;
    transition:0.3s;
}

/* menu */
.menu--area > nav{
    max-width:1020px;
    height:65px;
    padding:0px 20px;
    margin:0 auto;

    display:flex;
    justify-content: space-between;
    align-items: center;
}
.menu--logo{
    font-size:1.8rem;
    letter-spacing: -1px;
    font-weight:700;
}
.menu--list{
    display:flex;
    align-items: center;
    height:100%;
}
.menu--list li{
    margin:0px 7px;
}
.menu--list li a{
    text-transform:uppercase;
    color:#535353;
    font-weight: 500;
    font-size:0.95rem;
}
.menu--list li a:hover{
    color:#4DB7C6;
    transition:0.3s;
}
.menu--listSpecial{
    display:flex;
    align-items: center;
    height:100%;
    padding:0px 15px;
    background:linear-gradient(90deg, rgba(62,152,167,1) 7%, rgba(63,154,169,1) 45%, rgba(77,183,198,1) 92%);
}
.menu--listSpecial:hover{
    transition:0.3s;
    opacity:0.7;
    cursor: pointer;
}
.menu--listSpecial a{
    color:white !important;
}