.slideHome--area{
    width:100%;
    height:550px;
    background:url('./slide-home.jpg');
    background-size:100% 100%;
    position:relative;

    display:flex;
    align-items: center;
}
.slideHome--area::after{
    content: " ";
    position: absolute;
    width:100%;
    height:100%;
    background:black;
    opacity:0.8;
}.slideHome--area > div{
    width:650px;
    margin-left:50%;
    position:relative;
    z-index:2;
    opacity:0;
}
.slideHome--title{
    color:white;
    font-size:2.4rem;
    letter-spacing: -1px;
    font-weight: 500;
    line-height: 35px;
}
.slideHome--text{
    color:white;
    font-size:1.25rem;
    font-weight: 300;
    margin-top:15px;
    line-height: 27px;
    opacity:0.8;
}
.slideHome--link{
    margin-top:30px;
    display:flex;
}
.slideHome--linkLogin,
.slideHome--linkCreate{
    display:block;
    width:140px;
    padding:10px 0px;
    border-radius:5px;
    text-align:center;
    font-size:1.1rem;
    font-weight:300;
}
.slideHome--linkLogin{
    color:rgb(129, 224, 231);
    border:2px solid rgb(129, 224, 231);
    margin-right:15px;
}
.slideHome--linkCreate{
    background:linear-gradient(90deg, rgb(62, 152, 167) 7%, rgb(63, 154, 169) 45%, rgb(77, 183, 198) 92%);
    color:white;
    padding-top:12px;
}

/* effects link */
.slideHome--linkLogin:hover{
    transition:0.3s;
    border:2px solid white;
    color:white;
}
.slideHome--linkCreate:hover{
    opacity: 0.7;
    transition:0.3s;
}