.footer--links{
    width:100%;
    background:linear-gradient(90deg, rgba(0,30,34,1) 0%, rgba(1,56,64,1) 44%, rgba(1,52,59,1) 84%);
}
.footer--links > div{
    max-width:1020px;
    margin:0 auto;
    padding:60px 0px;

    display:flex;
    justify-content: space-between;
    align-items: flex-start;
}
.footer--into{
    width:250px;
}
.footer--into > h2{
    color:white;
    font-size:1.5rem;
}
.footer--into > p{
    color:white;
    font-size:1rem;
    margin-top:5px;
    opacity:0.7;
    font-weight:300;
}
.footer--icons{
    width:40%;
    margin-top:20px;
    display:flex;
    justify-content: space-between;
}
.footer--icon--face,
.footer--icon--twitter,
.footer--icon--instagram{
    display:block;
    width:25px;
    height:25px;
    background:url('./icons/icon-face.png');
    opacity: 0.7;
}
.footer--icon--face{
    background:url('./icons/icon-face.png');
    background-size:100% 100%;
}
.footer--icon--twitter{
    background:url('./icons/icon-twitter.png');
    background-size:100% 100%;
}
.footer--icon--instagram{
    background:url('./icons/icon-instagram.png');
    background-size:100% 100%;
}

.footer--menu > nav > h3{
    color:white;
    font-weight:400;
    font-size:1.1rem;
}
.footer--menu > nav > ul{
    margin-top:12px;
}
.footer--menu > nav > ul > li > a{
    color:white;
    font-size:1rem;
    opacity:0.7;
    font-weight:300;
    margin:5px 0px;
    display:block;
}
.footer--menu > nav > ul > li > a:hover{
    opacity:0.4;
    transition:0.3s;
}
.footer--copyright{
    padding:10px 0px;
	background:#011f24;
	border-top:1px solid #133337;
    text-align:center;
}
.footer--copyright > span{
    color:white;
    font-weight: 300;
    font-size:0.9rem;
}