.topCourses--courses{
    max-width:950px;
    margin:50px auto;
    margin-bottom:60px;

    display: flex;
    justify-content: center;
    align-items: flex-end;
}
.coursesCard--card{
    width:300px;
    height:455px;
    border:1px solid #ddd;
    position:relative;
    margin-top: 100px;
}
.coursesCard--card:nth-child(1){
    border-right:none;
}
.coursesCard--card:nth-child(3){
    border-left:none;
}

.coursesCard--img{
    width:100%;
    height:220px;
}
.coursesCard--title{
    font-size:1.2rem;
    text-align:center;
    color:#343434;
    margin-top:20px;
    padding:0px 5px;
}
.coursesCard--text{
    font-size:1.02rem;
    text-align:center;
    color:#616060;
    margin-top:15px;
    padding:0px 5px;
    margin-bottom:20px;
    font-weight: 300;
}
.coursesCard--link{
    background:linear-gradient(90deg, rgba(62,152,167,1) 7%, rgba(63,154,169,1) 45%, rgba(77,183,198,1) 92%);
    border-radius:5px;
    padding:12px 30px;
    color:white;
    font-size:1.05rem;
    font-weight: 300;
    
    position:absolute;
    bottom:30px;
    left: 45%;
    margin-left: -50px;
}
.coursesCard--link:hover{
    opacity:0.7;
    transition:0.3s;
}
.topCourses--allCourses{
    color:white;
    border-radius:35px;
    padding:17px 0px;
    font-family:'Ubuntu', sans-serif;
    font-weight:500;
    letter-spacing: -1px;
    font-size:1.7rem;

    background:linear-gradient(90deg, rgba(0,30,34,1) 0%, rgba(1,56,64,1) 44%, rgba(1,52,59,1) 84%);
    display:block;
    width:350px;
    text-align:center;
    margin:30px auto;
}
.topCourses--allCourses:hover{
    opacity:0.8;
    transition:0.3s;
}

/* card main */
.coursesCard--card:nth-child(2){
    position:relative;
    border:none;
    z-index:1;
    height:510px;
    box-shadow:0px 0px 3px 1px #d3d3d3;
}